import React,{useState} from 'react'
import { Icon } from '@grafana/ui';

const HttpAccessHelp = () => (
    <div className="grafana-info-box m-t-2" style={{width:'100%'}}>
      <p>
        Select Authentication Type 
      </p>
      <p>
        Post — credentials pass in the request body as part of the token request.
      </p>
      <p>
        Basic — credentials pass in the basic authorization header as part of the token request.
      </p>
    </div>
  );

export const HelpText: React.FC = (props) => {
    const [isAccessHelpVisible, setIsAccessHelpVisible] = useState(false);

  return (
    <>
        <div className="gf-form">
            <label
            className="gf-form-label query-keyword pointer"
            onClick={() => setIsAccessHelpVisible((isVisible) => !isVisible)}
            >
                Help&nbsp;
            <Icon name={isAccessHelpVisible ? 'angle-down' : 'angle-right'} style={{ marginBottom: 0 }} />
            </label>
        </div>
        {isAccessHelpVisible && <HttpAccessHelp />}
    </>
  )
}
