enum FieldsType {
  String = 'string',
  Timeseries = 'timeseries',
  Complex = 'complex',
  Number = 'number',
  Double = 'double',
  Boolean = 'boolean',
  Timestamp = 'timestamp',
  Source = 'source',
  CSV = 'csv',
  Long = 'long',
}

type FieldObject = {
  name: string;
  type: string;
  labels: Labels;
  values: any[];
};

type Labels = {
  metric: string;
};

type MetaObj = {
  isAttribute: boolean;
  isNumber: boolean;
  index: number;
  isTimeSeries: boolean;
  isMetric: boolean;
  aliasName?: string;
  type: string;
  innerMetaData?: Map<string, InnerObject>;
  isSource?: boolean;
};

type InnerObject = {
  isNumber: boolean;
  index: number;
  isTimeStamp: boolean;
  aliasName: string;
  type: string;
  isConsumpFunc?: boolean;
};

type TimeObject = {
  name: string;
  type: string;
  values: any[];
};

/**
 * Trims brackets from attributes or metric name, to make it readable if no explicit alias name is provided.
 * @param aliasName Raw attribute or metric name
 * @returns Alias name for attributes or metrics
 */
function trimWraper(aliasName: string): string {
  let key = aliasName;
  if (key.includes('(')) {
    key = key.substring(key.trim().indexOf('(') + 1, key.trim().lastIndexOf(')'));
  }
  return key;
}

const consumpFuncArray = ['min', 'max', 'count', 'group_count', 'count', 'sum'];

/**
 * Transforming raw UQL reponse into grafana dataframes
 * @param resp UQL query response
 * @param queryText UQL query to be executed
 * @returns Transformed grafana dataframe array
 */
export function dataTranformationHandler(resp: any, queryText: string): object {
  let resultArray: any[] = [];
  let input: any = resp;

  const metaDataMap = new Map<string, MetaObj>();
  const dataMap = new Map<string, object>();
  const onlyAttributeMap = new Map<string, object>();
  const entityAttributes = new Map<string, object>();

  let stringIndex: number;
  let arrayCount: number;

  const isConsumption = queryText.includes('{');

  //Reads model from UQL query output.
  input.forEach((modelItems: any) => {
    if (modelItems.type === 'model') {
      let modelFields = modelItems.model.fields;

      // Traverse model object from UQL query output to collect metadata information
      modelFields.forEach((fieldItem: any, fieldIdx: number) => {
        metaDataMap.set(fieldItem.alias, {
          isAttribute:
            (fieldItem.type === FieldsType.Number ||
            fieldItem.type === FieldsType.String ||
            fieldItem.type === FieldsType.Boolean ||
            fieldItem.type === FieldsType.Double ||
            fieldItem.type === FieldsType.CSV ||
            fieldItem.type === FieldsType.Long ||
            fieldItem.alias.includes('attribute'))
              ? true
              : false,
          index: fieldIdx,
          type: fieldItem.type,
          isTimeSeries: fieldItem.type === FieldsType.Timeseries ? true : false,
          isMetric:
            (fieldItem.type === FieldsType.Timeseries ||
            fieldItem.type === FieldsType.Complex ||
            fieldItem.alias.includes('metrics'))
              ? true
              : false,
          aliasName: fieldItem.alias,
          isNumber: fieldItem.type === FieldsType.Number ? true : false,
          isSource: fieldItem.alias === FieldsType.Source ? true : false,
        });

        if (fieldItem.type === FieldsType.String) {
          stringIndex = fieldIdx;
        }

        // Traverse inner model object from UQL query output to collect metadata information
        if (Object.keys(fieldItem).indexOf('model') !== -1) {
          const innerMetaDataMap = new Map<string, InnerObject>();
          fieldItem.model.fields.forEach((innerItem: any, inIdx: number) => {
            // Finding metadata information if query do not have source
            if (innerItem.alias !== 'source') {
              // Traverse inner model object to find metadata information if query has nested model object
              if (innerItem['model'] !== undefined) {
                innerItem.model.fields.forEach((lastThirdItem: any, inIdx: number) => {
                  innerMetaDataMap.set(lastThirdItem.alias, {
                    index: inIdx,
                    type: lastThirdItem.type,
                    isTimeStamp: lastThirdItem.type === FieldsType.Timestamp ? true : false,
                    aliasName: lastThirdItem.alias,
                    isNumber: lastThirdItem.type === FieldsType.Number ? true : false,
                    isConsumpFunc: consumpFuncArray.includes(lastThirdItem.alias),
                  });

                  let fieldItemsObj = metaDataMap.get(fieldItem.alias);
                  if (fieldItemsObj !== undefined) {
                    fieldItemsObj.innerMetaData = innerMetaDataMap;
                  }
                });
              } else {
                // Collecting metadata information if query do not have nested model object
                innerMetaDataMap.set(innerItem.alias, {
                  index: inIdx,
                  type: innerItem.type,
                  isTimeStamp: innerItem.type === FieldsType.Timestamp ? true : false,
                  aliasName: innerItem.alias,
                  isNumber: innerItem.type === FieldsType.Number ? true : false,
                  isConsumpFunc: consumpFuncArray.includes(innerItem.alias),
                });

                let fieldItemsObj = metaDataMap.get(fieldItem.alias);
                if (fieldItemsObj !== undefined) {
                  fieldItemsObj.innerMetaData = innerMetaDataMap;
                }
              }
            }

            // Collecting metadata information if source is present
            else {
              let fieldItemsObj = metaDataMap.get(fieldItem.alias);
              if (fieldItemsObj !== undefined) {
                fieldItemsObj.isSource = innerItem.alias === FieldsType.Source ? true : false;
              }
            }
          });
        }
      });
    }
  });

  // Reads data set mapping from UQL query output.
  input.forEach((el: any) => {
    if (el.type === 'data' && el.dataset !== 'd:main' && el.data.length > 0) {
      // Extract data mapping from UQL output when consumption function is absent.
      if (!isConsumption) {
        let timeStamp: any = [];
        let values: any = [];
        let timeObject: { [key: string]: string } = {};

        let dataFieldsArray = el.data.length !== 1 ? el.data : el.data[0][1].length > 1 ? el.data[0][1] : el.data;

        dataFieldsArray.forEach((loopItem: any, lindx: number) => {
          if (loopItem.length > 0) {
            let currentZone = new Date(loopItem[0]);
            let epocTimeZone = currentZone.getTime();
            timeStamp.push(epocTimeZone);
            values.push(loopItem[1]);
          }
        });

        if (resultArray.length === 0 && !(resultArray.length > 0) && !el.dataset.includes('d:attributes')) {
          timeObject = {
            name: 'Time',
            type: 'time',
            values: timeStamp,
          };
          resultArray.push(timeObject);
        }
        arrayCount = values.length;
        dataMap.set(`${el.dataset}`, values);

        for (let valueObj of Array.from(metaDataMap.values())) {
          // Extract data mapping, if query has only entity attributes
          if (valueObj.isAttribute && el.dataset.includes('d:attributes')) {
            for (let [inkey, subMetaObj] of valueObj.innerMetaData as Map<string, InnerObject>) {
              let attributeArray: any = [];

              for (let dataItem of el.data) {
                let onlyAttributeObj: TimeObject = { name: '', type: '', values: [] };
                dataItem.forEach((metricData: any, idx: number) => {
                  if (idx === subMetaObj.index && subMetaObj.aliasName === inkey) {
                    attributeArray.push(metricData);
                    onlyAttributeObj.name = inkey;
                    onlyAttributeObj.type = 'string';
                    onlyAttributeObj.values = attributeArray;
                    entityAttributes.set(inkey, onlyAttributeObj);
                  }
                });
              }
            }
          }
        }
      }

      //  If consumption function is present, extract data mapping from UQL output.
      else {
        for (let valueObj of Array.from(metaDataMap.values())) {
          let consumptionArray: any = [];
          let timeStampArray: any = [];
          let timeStampObj: TimeObject = { name: 'time', type: 'time', values: [] };

          if (valueObj.isMetric) {
            let elData = !valueObj.isSource ? el.data : el.data[0][1];

            for (let dataItem of elData) {
              dataItem.forEach((metricData: any, idx: number) => {
                for (let [inkey, subMetaObj] of valueObj.innerMetaData as Map<string, InnerObject>) {
                  if (
                    subMetaObj.isNumber &&
                    typeof metricData !== 'string' &&
                    idx === subMetaObj.index &&
                    subMetaObj.aliasName === inkey
                  ) {
                    if (typeof metricData === 'object' && metricData !== null) {
                      metricData.forEach((metricItem: any) => {
                        dataMap.set(`${el.dataset}`, metricItem);
                      });
                    } else {
                      consumptionArray.push(metricData);
                      dataMap.set(`${el.dataset}`, consumptionArray);
                    }
                  }
                  if (
                    subMetaObj.isTimeStamp &&
                    typeof metricData === 'string' &&
                    idx === subMetaObj.index &&
                    subMetaObj.aliasName === inkey
                  ) {
                    let currentZone = new Date(metricData);
                    let epocTimeZone = currentZone.getTime();
                    timeStampArray.push(epocTimeZone);
                    timeStampObj.values = timeStampArray;
                  }
                }
              });
            }

            if (resultArray.length === 0 && !(resultArray.length > 0) && timeStampArray.length !== 0) {
              resultArray.push(timeStampObj);
            }

            arrayCount = timeStampArray.length;
          }
        }
      }
    }
  });

  // Traverses and reads m:main model from UQL output.
  input.forEach((el: any) => {
    if (el.type === 'data' && el.dataset === 'd:main' && entityAttributes.size === 0) {
      // Extract d:main mapping from UQL output when consumption function is absent
      if (!isConsumption) {
        for (let [key, valueObj] of metaDataMap) {
          let serviceArray: any = [];

          for (let item of el.data) {
            let metricObj: FieldObject = { name: '', type: 'number', labels: { metric: ' ' }, values: [] };
            let attributeObj: TimeObject = { name: '', type: '', values: [] };

            item.forEach((metricDataArray: any, idx: number) => {
              // If its a metric, push to result array.
              if (valueObj.isMetric && idx === valueObj.index) {
                const value: any = dataMap.get(metricDataArray.$dataset);
                metricObj.name = trimWraper(key);
                metricObj.values = value;
                metricObj.labels.metric = item[stringIndex];
                resultArray.push(metricObj);
              }

              // If its attribute, push to result array.
              if (valueObj.isAttribute && idx === valueObj.index) {
                if (arrayCount > 0) {
                  for (let i = 0; i < arrayCount; i++) {
                    attributeObj.values.push(metricDataArray);
                  }
                  attributeObj.name = trimWraper(key);
                  attributeObj.type = valueObj.type;
                  resultArray.push(attributeObj);
                }

                // Creates 'attribute only' data for Grafana dataframes.
                else {
                  attributeObj.name = trimWraper(key);
                  attributeObj.type = valueObj.type;
                  serviceArray.push(metricDataArray);
                  attributeObj.values = serviceArray;
                  onlyAttributeMap.set(key, attributeObj);
                }
              }
            });
          }
        }
      }

      // Extract d:main mapping from UQL output when consumption function is present
      else {
        for (let [key, valueObj] of metaDataMap) {
          for (let item of el.data) {
            item.forEach((metricData: any, idx: number) => {
              let attribute: TimeObject = { name: '', type: 'number', values: [] };

              if (valueObj.isMetric && typeof metricData !== 'string' && idx === valueObj.index) {
                const values: any = dataMap.get(metricData.$dataset);

                for (let [inkey, subMetaObj] of valueObj.innerMetaData as Map<string, InnerObject>) {
                  let metricsObj: FieldObject = { name: '', type: 'number', labels: { metric: ' ' }, values: [] };

                  // If consumption function is present
                  if (subMetaObj.isConsumpFunc && values !== undefined) {
                    values.forEach((metricData: any, inidx: number) => {
                      // Push metric to result array when timestamp is absent
                      if (arrayCount === 0) {
                        if (subMetaObj.isNumber && inidx === subMetaObj.index && subMetaObj.aliasName === inkey) {
                          metricsObj.values.push(metricData);
                          metricsObj.name = trimWraper(key);
                          metricsObj.type = subMetaObj.type;
                          metricsObj.labels.metric =
                            stringIndex !== undefined ? trimWraper(inkey) + ' ' + item[stringIndex] : trimWraper(inkey);
                          resultArray.push(metricsObj);
                        }
                      }

                      // Push metric to result array when timestamp is present
                      else {
                        if (subMetaObj.isNumber && subMetaObj.aliasName === inkey && inidx < arrayCount) {
                          metricsObj.values.push(metricData);
                          metricsObj.name = trimWraper(key);
                          metricsObj.type = subMetaObj.type;
                          metricsObj.labels.metric =
                            stringIndex !== undefined ? trimWraper(inkey) + ' ' + item[stringIndex] : trimWraper(inkey);
                          onlyAttributeMap.set(inkey, metricsObj);
                        }
                      }
                    });
                  }

                  // If consumption function is absent push metric to result array
                  if (!subMetaObj.isConsumpFunc && subMetaObj.isNumber && values !== undefined) {
                    metricsObj.values = values;
                    metricsObj.name = trimWraper(key);
                    metricsObj.type = subMetaObj.type;
                    metricsObj.labels.metric = stringIndex !== undefined ? item[stringIndex] : '';
                    resultArray.push(metricsObj);
                  }
                }
              }

              //  If its attribute, push to result array.
              if (valueObj.isAttribute && idx === valueObj.index && typeof metricData !== 'object') {
                if (arrayCount > 0) {
                  for (let i = 0; i < arrayCount; i++) {
                    attribute.values.push(metricData);
                  }
                  attribute.name = trimWraper(key);
                  attribute.type = valueObj.type;
                  resultArray.push(attribute);
                } else {
                  attribute.name = trimWraper(key);
                  attribute.type = valueObj.type;
                  attribute.values.push(metricData);
                  resultArray.push(attribute);
                }
              }
            });
          }
        }
      }
    }
  });

  // Update resultArray for 'only attribute' UQL query.
  if (onlyAttributeMap.size > 0) {
    onlyAttributeMap.forEach((item) => {
      resultArray.push(item);
    });
  }

  // Updates resultArray with entity attributes data.
  if (entityAttributes.size > 0) {
    entityAttributes.forEach((item) => {
      resultArray.push(item);
    });
  }

  return resultArray;
}
