import defaults from 'lodash/defaults';
import React, { PureComponent, MouseEvent } from 'react';
import { QueryEditorProps } from '@grafana/data';
import { getBackendSrv } from '@grafana/runtime';
import { DataSource } from './datasource';
import { lastValueFrom } from 'rxjs';
import { SelectComp } from './components/Select';
import { AppdOptions, MyQuery, defaultQuery } from './types';
import './QueryEditor.css';
import {
  Select,
  Switch,
  Button,
  Icon,
  TextArea,
  InlineLabel,
  RadioButtonGroup,
  ButtonCascader,
  Input,
  Card,
  Tooltip,
  Alert,
} from '@grafana/ui';

type Props = QueryEditorProps<DataSource, MyQuery, AppdOptions>;

interface State {
  attributeValue: any;
  tagFilterList: any;
  attributeFilterList: any;
  finalQuery: string;
  nameArrayOptions: any[];
  entityArrayOptions: any[];
  attributeArrayOptions: any[];
  attributeFilterArrayOptions: any[];
  tagsArrayOptions: any;
  metricArrayOptions: any[];
  nameEntitiesMap: Map<any, any>;
  entitiesMatricMap: Map<any, any>;
  entitiesAttributeMap: Map<any, any>;
  filterCascadeOptions: any;
  mathOperations: any;
  sourceValue: any;
  isSourceError: any;
}

export class QueryEditor extends PureComponent<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      attributeValue: '',
      tagFilterList: [],
      attributeFilterList: [],
      finalQuery: '',
      nameArrayOptions: [],
      entityArrayOptions: [],
      metricArrayOptions: [],
      tagsArrayOptions: [],
      attributeArrayOptions: [],
      attributeFilterArrayOptions: [],
      nameEntitiesMap: new Map(),
      entitiesMatricMap: new Map(),
      entitiesAttributeMap: new Map(),
      filterCascadeOptions: [],
      // supported consumption functions for UQL query
      mathOperations: [
        { value: '', label: 'Choose' },
        { value: 'min', label: 'min' },
        { value: 'max', label: 'max' },
        { value: 'sum', label: 'sum' },
        { value: 'count', label: 'count' },
        { value: 'group_count', label: 'group count' },
      ],
      sourceValue: '',
      isSourceError: [],
    };
  }

  /**
   * fetches new token if not already available 
   * @returns authorization token
   */
  async getToken(): Promise<any> {
    if (this.props.datasource.token === '') {
      await this.props.datasource.generateToken(false);
    }
    return this.props.datasource.token;
  }

  async componentDidMount() {
    let nameEntitiesMap = new Map();
    let entitiesMatricMap = new Map();
    let entitiesAttributeMap = new Map();
    await this.getToken();
    await this.fetchSchema().then((response) => {
      let data: any[] = [];
      data = response.data.items;
      for (let i = 0; i < data.length; i++) {
        let nameSpace = data[i].namespace.name;
        if (nameEntitiesMap.has(nameSpace)) {
          let array = nameEntitiesMap.get(nameSpace);
          array.push(data[i].name);
        } else {
          let array: any[] = [];
          array.push(data[i].name);
          nameEntitiesMap.set(nameSpace, array);
        }
        let entity = data[i].name;
        let matric = data[i].metricTypes;
        let attributes = Object.keys(data[i].attributeDefinitions.attributes);
        //Populate dropdown for metrics and attribute based on selected entity
        entitiesMatricMap.set(nameSpace + ':' + entity, matric);
        entitiesAttributeMap.set(nameSpace + ':' + entity, attributes);
        let nameArray = [...nameEntitiesMap.keys()];
        let defaultName = [{ value: '', label: 'Choose Namespace...' }];
        nameArray.sort().map((value) => defaultName.push({ value: value, label: value }));
        this.setState({
          nameEntitiesMap: nameEntitiesMap,
          entitiesMatricMap: entitiesMatricMap,
          entitiesAttributeMap: entitiesAttributeMap,
        });
        nameArray = [...this.state.nameArrayOptions, { value: '', label: 'Choose Namespace...' }];
        this.setState({ nameArrayOptions: defaultName });
      }
    });

    // Populate dropdown for tag selection for assisted query workflow 
    await this.fetchTags().then((response) => {
      let data: any[] = [];
      data = Object.keys(response.data.items);
      this.setState({ tagsArrayOptions: data.sort((a, b) => b.localeCompare(a)).reverse().map((value: any) => ({ value: value, label: value })) });
    });

    if (this.props.query.nameValue.value !== undefined) {
      this.setNamespaceValue(this.props.query.nameValue);
    }

    if (this.props.query.entityValue.value !== undefined) {
      this.setEntityValue(this.props.query.entityValue);
    }

    // Populate dropdown for attribute filter selection for assisted query workflow 
    if (this.props.query.attributeTagValue !== undefined && typeof this.props.query.attributeTagValue === 'object') {
      this.props.query.attributeTagValue.forEach(async (attributeTagValue: any, index: number) => {
        await this.fetchAttributeFilterArr(
          `fetch attributes ("${attributeTagValue}") from entities (${this.props.query.entityValue.value})`
        ).then((response) => {
          let res: any = [];
          res = response.data;
          const attribute = [...this.state.attributeFilterList];
          attribute[index] = [...new Set([].concat(...res[1].data))]
            .sort()
            .map((value: any) => ({ value: value, label: value }));
          this.setState({ attributeFilterList: attribute });
        });
        // Populate dropdown for tag filter selection for assisted query workflow 
        await this.fetchTagFilterArr(attributeTagValue).then((response) => {
          let data: any = [];
          data = [...new Set(response.data.items)];
          const tag = [...this.state.tagFilterList];
          tag[index] = data.sort().map((value: any) => ({ value: value, label: value }));
          this.setState({ tagFilterList: tag });
        });
      });
    }
  }

  /**
   * fetches schema information with API call for assisted workflow 
   * @returns API promise response 
   */
  async fetchSchema(): Promise<any> {
    const observableResponse = getBackendSrv().fetch({
      method: 'GET',
      url: `${this.props.datasource.jsonData.url}/monitoring/v1/types/entity?max=1200`,
      headers: {
        Authorization: 'bearer ' + this.props.datasource.token,
      },
    });

    const response = await lastValueFrom(observableResponse);
    return response;
  }

  /**
   * fetches tags with API call for assisted workflow 
   * @returns API promise response
   */
  async fetchTags(): Promise<any> {
    const observableResponse = getBackendSrv().fetch({
      method: 'GET',
      url: `${this.props.datasource.jsonData.url}/tags/v1/keys`,
      headers: {
        Authorization: 'bearer ' + this.props.datasource.token,
      },
    });
    const response = await lastValueFrom(observableResponse);
    return response;
  }

  /**
   * fetches attribute values filter list based on attribute 
   * @param attrQuery  attribute query 
   * @returns API promise response
   */
  async fetchAttributeFilterArr(attrQuery: String): Promise<any> {
    const headers = {
      authorization: 'bearer ' + (await this.getToken()),
      'appd-tid': this.props.datasource.jsonData.appdtid,
      'Content-Type': 'text/plain',
      Accept: 'application/json',
    };
    const observableResponse = getBackendSrv().fetch({
      url: this.props.datasource.jsonData.url + '/monitoring/v1/query/execute',
      method: 'POST',
      data: attrQuery,
      headers: headers,
    });
    const response = await lastValueFrom(observableResponse);
    return response;
  }
  /**
   * fetches source list based on metric
   * @param sourceQuery source query 
   * @returns API promise response
   */
  async fetchSourceArr(sourceQuery: String): Promise<any> {
    const headers = {
      authorization: 'bearer ' + (await this.getToken()),
      'appd-tid': this.props.datasource.jsonData.appdtid,
      'Content-Type': 'text/plain',
      Accept: 'application/json',
    };
    const observableResponse = getBackendSrv().fetch({
      url: this.props.datasource.jsonData.url + '/monitoring/v1/query/execute',
      method: 'POST',
      data: sourceQuery,
      headers: headers,
    });
    const response = await lastValueFrom(observableResponse);
    return response;
  }

  /**
   * fetches tag values filter list based on tagName
   * @param tagName tag name
   * @returns API promise response
   */
  async fetchTagFilterArr(tagName: any): Promise<any> {
    const observableResponse = getBackendSrv().fetch({
      method: 'GET',
      url: `${this.props.datasource.jsonData.url}/tags/v1/keys/${tagName}`,
      headers: {
        Authorization: 'bearer ' + (await this.getToken()),
      },
    });
    const response = await lastValueFrom(observableResponse);
    return response;
  }

  onQueryTextChange = (event: any) => {
    const { onChange, query } = this.props;
    onChange({ ...query, queryText: event.target.value });
  };

  onRunQuery = (event: MouseEvent<HTMLButtonElement>) => {
    this.props.onRunQuery();
  };

  /**
   * sets entity value and corresponding metrics and attributes array 
   * @param entityValue entity value 
   */
  setEntityValue = (entityValue: any) => {
    const { onChange, query } = this.props;
    onChange({ ...query, entityValue: entityValue });
    let metricArray = this.state.entitiesMatricMap.get(entityValue.value);
    let attributeArray = this.state.entitiesAttributeMap.get(entityValue.value);
    let attributeArr = [{ value: '', label: 'Choose Attribute...' }];
    let metricsArr = [{ value: '', label: 'Choose Metrics...' }];
    attributeArray !== undefined &&
      attributeArray.sort().map((value: any) => attributeArr.push({ value: value, label: value }));
    metricArray !== undefined &&
      metricArray.sort().map((value: any) => metricsArr.push({ value: value, label: value }));
    this.setState({ metricArrayOptions: metricsArr });
    this.setState({ attributeArrayOptions: attributeArr });
    this.setState({
      attributeFilterArrayOptions:
        attributeArray !== undefined && attributeArray.sort().map((value: any) => ({ value: value, label: value })),
    });
  };

  /**
   * sets namespace value and corresponding entity array 
   * @param namespaceValue namespace value
   */
  setNamespaceValue = (namespaceValue: any) => {
    const { onChange, query } = this.props;
    onChange({ ...query, nameValue: namespaceValue });
    let entityArray = this.state.nameEntitiesMap.get(namespaceValue.value);
    let entity = [{ value: '', label: 'Choose Entity Type..' }];
    entityArray
      .sort()
      .map((value: any) => entity.push({ value: namespaceValue.value + ':' + value, label: namespaceValue.value + ':' + value }));
    this.setState({ entityArrayOptions: entity });
  };

  /**
   * sets metric value and corresponding source array
   * @param value metric value
   * @param i metric index 
   */
  setMetricValue = async (value: any, i: any) => {
    const arr = [...this.props.query.metricDivValue];
    arr[i] = `${value.value}`;
    const { onChange, query } = this.props;
    let source: any = [];
    await this.fetchSourceArr(
      `fetch sources ("${value.value}") from entities (${this.props.query.entityValue.value})`
    ).then((response) => {
      let res: any = [];
      res = response.data;
      source = [...query.sourceArrayOptions];
      source[i] = []
        .concat(...res[2].data)
        .sort()
        .map((value: any) => ({ value: value, label: value }));
      this.setState(() => {
        if (source[i].length === 0) {
          this.onFormQuery();
        } else {
          const sourceError = [...this.state.isSourceError];
          sourceError[i] = true;
          this.setState({ isSourceError: sourceError });
        }
      });
    });
    onChange({ ...query, metricDivValue: arr, sourceArrayOptions: source });
  };

  setWithoutFilterAttributeValue = (value: any) => {
    const { onChange, query } = this.props;
    onChange({ ...query, attributeValue: value });
    this.setState({}, () => {
      if (!this.state.isSourceError.includes(true)) {
        this.onFormQuery();
      }
    });
  };

  /**
   * deletes metrics with index i
   * @param i metric index 
   */
  deleteMetrics = (i: any) => {
    const { onChange, query } = this.props;
    const arr = [...query.metricDivValue];
    const arr1 = [...query.sourceArrayOptions];
    const arr2 = [...query.sourceValueArray];
    const arr3 = [...this.state.isSourceError];
    let deletedValue = arr[i];
    arr.splice(i, 1);
    arr1.splice(i, 1);
    arr2.splice(i, 1);
    arr3.splice(i, 1);
    onChange({ ...query, metricDivValue: arr, sourceValueArray: arr2, sourceArrayOptions: arr1 });
    this.setState({},() => {
      if (deletedValue !== '' && deletedValue !== undefined) {
        if (!this.state.isSourceError.includes(true)) {
          this.onFormQuery();
        }
      }
    });
    this.setState({ isSourceError: arr3 }, () => {});
  };

  /**
   * adds filterOption(attribute or tag)
   * @param filterOption 
   */
  handleCascaderChange = (filterOption: any) => {
    const { onChange, query } = this.props;
    let attributeTagValue = query.attributeTagValue === undefined ? [] : query.attributeTagValue;
    let operatorvalue = query.operatorvalue === undefined ? [] : query.operatorvalue;
    let attributeTagFilterValue = query.attributeTagFilterValue === undefined ? [] : query.attributeTagFilterValue;
    let attributeDivValue = query.attributeDivValue === undefined ? [] : query.attributeDivValue;
    const arr1 = [...attributeTagValue, ''];
    const arr2 = [...attributeTagFilterValue, ''];
    const arr3 = [...operatorvalue, '='];
    const arr4 = [...this.state.tagFilterList, ''];
    const arr5 = [...this.state.attributeFilterList, ''];
    if (filterOption.includes('Attribute')) {
      const arr = [...attributeDivValue, 'attribute'];
      onChange({
        ...query,
        attributeDivValue: arr,
        attributeTagValue: arr1,
        attributeTagFilterValue: arr2,
        operatorvalue: arr3,
      });
      this.setState({
        tagFilterList: arr4,
        attributeFilterList: arr5,
      });
    } else {
      const arr = [...attributeDivValue, 'tag'];
      onChange({
        ...query,
        attributeDivValue: arr,
        attributeTagValue: arr1,
        attributeTagFilterValue: arr2,
        operatorvalue: arr3,
      });
      this.setState({
        tagFilterList: arr4,
        attributeFilterList: arr5,
      });
    }
  };
  /**
   * deletes filterOption (attribute or tag) with index 1
   * @param i index 
   */
  deleteAttributeTag(i: any) {
    const { onChange, query } = this.props;
    const arr = [...query.attributeDivValue];
    const arr1 = [...query.attributeTagValue];
    const arr2 = [...query.attributeTagFilterValue];
    const arr3 = [...query.operatorvalue];
    const arr4 = [...this.state.tagFilterList];
    const arr5 = [...this.state.attributeFilterList];
    let deletedValue = arr1[i];
    arr.splice(i, 1);
    arr1.splice(i, 1);
    arr2.splice(i, 1);
    arr3.splice(i, 1);
    arr4.splice(i, 1);
    arr5.splice(i, 1);
    this.setState(
      {
        tagFilterList: arr4,
        attributeFilterList: arr5,
      },
      () => {
        if (deletedValue !== '' && deletedValue !== undefined) {
          if (!this.state.isSourceError.includes(true)) {
            this.onFormQuery();
          }
        }
      }
    );
    onChange({
      ...query,
      attributeDivValue: arr,
      attributeTagValue: arr1,
      attributeTagFilterValue: arr2,
      operatorvalue: arr3,
    });
  }

  /**
   * sets tag value and corresponding tag filter array 
   * @param value tag value 
   * @param i  index 
   */
  filterTagHandler = async (value: any, i: any) => {
    const { onChange, query } = this.props;
    const arr = [...query.attributeTagValue];
    arr[i] = `${value.value}`;
    onChange({ ...query, attributeTagValue: arr });

    await this.fetchTagFilterArr(value.value).then((response) => {
      let data: any = [];
      data = [...new Set(response.data.items)];
      const tag = [...this.state.tagFilterList];
      tag[i] = data.sort().map((value: any) => ({ value: value, label: value }));
      this.setState({ tagFilterList: tag });
    });
  };

  /**
   * sets tag filter value
   * @param value tag filter value 
   * @param i index 
   */
  setTagFilterListValue = (value: any, i: any) => {
    const { onChange, query } = this.props;
    const arr = [...query.attributeTagFilterValue];
    arr[i] = `${value.value}`;
    onChange({ ...query, attributeTagFilterValue: arr });
    this.setState({}, () => {
      if (!this.state.isSourceError.includes(true)) {
        this.onFormQuery();
      }
    });
  };

  /**
   * sets attribute value and corresponding attribute filter array 
   * @param attrvalue attribute value 
   * @param i  index 
   */
  filterAttributeHandler = async (attrvalue: any, i: any) => {
    const { onChange, query } = this.props;
    const arr = [...query.attributeTagValue];
    arr[i] = `${attrvalue.value}`;
    onChange({ ...query, attributeTagValue: arr });

    await this.fetchAttributeFilterArr(
      `fetch attributes ("${attrvalue.value}") from entities (${this.props.query.entityValue.value})`
    ).then((response) => {
      let res: any = [];
      res = response.data;
      const attribute = [...this.state.attributeFilterList];
      attribute[i] = [...new Set([].concat(...res[1].data))]
        .sort()
        .map((value: any) => ({ value: value, label: value }));
      this.setState({ attributeFilterList: attribute });
    });
  };

  /**
   * sets attribute filter value
   * @param value attribute filter value 
   * @param i index 
   */
  setAttributeFilterListValue = (value: any, i: any) => {
    const { onChange, query } = this.props;
    const arr = [...query.attributeTagFilterValue];
    arr[i] = `${value.value}`;
    onChange({ ...query, attributeTagFilterValue: arr });
    this.setState({},() => {
      if (!this.state.isSourceError.includes(true)) {
        this.onFormQuery();
      }
    });
  };

  /**
   * sets selected  metric source
   * @param value source value 
   * @param i metric index 
   */
  setSourceValue = (value: any, i: any) => {
    const { onChange, query } = this.props;
    const arr = [...this.props.query.sourceValueArray];
    arr[i] = `${value.value}`;
    const arr1 = [...this.state.isSourceError];
    arr1[i] = false;
    onChange({ ...query, sourceValueArray: arr });
    this.setState({ isSourceError: arr1 }, () => {
      if (!this.state.isSourceError.includes(true)) {
        this.onFormQuery();
      }
    });
  };

  /**
   * 'QueryBox' and 'Assisted Query Workflow' tabs selection
   * @param queryEditorType query editor type
   */
  setQueryTypeEditor = (queryEditorType: any) => {
    const { onChange, query } = this.props;
    onChange({ ...query, queryTypeSelected: queryEditorType });
  };

  setOperatorSelected = (value: any, i: any) => {
    const { onChange, query } = this.props;
    const arr = [...query.operatorvalue];
    arr[i] = `${value.value}`;
    onChange({ ...query, operatorvalue: arr });
  };

  onToggleId = (event: any) => {
    const { onChange, query } = this.props;
    onChange({ ...query, id: event.target.checked });
    this.setState({}, () => {
      if (!this.state.isSourceError.includes(true)) {
        this.onFormQuery();
      }
    });
  };

  /**
   * sets consumption function value for assisted query workflow
   * @param value selected consumption function
   */
  setConsumptionValues = (value: any) => {
    const { onChange, query } = this.props;
    onChange({ ...query, mathOperationValue: value });
    this.setState({}, () => {
      if (!this.state.isSourceError.includes(true)) {
        this.onFormQuery();
      }
    });
  };

  /**
   * Generates UQL query based on selected dropdowns in 'Assisted Query Workflow'
   */
  onFormQuery = () => {
    const { query } = this.props;
    let loadMetricArray = query.metricDivValue === undefined ? [] : query.metricDivValue;
    let loadSourceArray = query.sourceValueArray === undefined ? [] : query.sourceValueArray;
    let metricValueArr = [...loadMetricArray];
    let sourceValueArr = [...loadSourceArray];
    let tagValueArr: any = [];
    let tagFilterValueArr: any = [];
    let tagOperator: any = [];
    let attributeValueArr: any = [];
    let attributeFilterValueArr: any = [];
    let attributeOperator: any = [];
    let loadAttributeDivValue = query.attributeDivValue === undefined ? [] : query.attributeDivValue;
    const arr = [...loadAttributeDivValue];

    for (let i = 0; i < arr.length; i++) {
      if (arr[i].includes('attribute')) {
        attributeValueArr.push(query.attributeTagValue[i]);
        attributeFilterValueArr.push(query.attributeTagFilterValue[i]);
        attributeOperator.push(query.operatorvalue[i]);
      } else {
        tagValueArr.push(query.attributeTagValue[i]);
        tagFilterValueArr.push(query.attributeTagFilterValue[i]);
        tagOperator.push(query.operatorvalue[i]);
      }
    }

    let count = 0;
    for (let metricValue of metricValueArr) {
      if (metricValue === '') {
        count += 1;
      }
    }

    let newQuery = 'FETCH';
    if (query.id !== false && query.id !== undefined) {
      newQuery +=
        (query.attributeValue && query.attributeValue.value !== '') ||
          (metricValueArr.length > 0 && metricValueArr.length - count !== 0)
          ? ' id, '
          : ' id ';
    }

    if (query.attributeValue !== undefined && query.attributeValue.value !== '') {
      newQuery +=
        metricValueArr.length > 0 && metricValueArr.length - count !== 0
          ? ' attributes  ' + `("${query.attributeValue.value}"),`
          : ' attributes  ' + `("${query.attributeValue.value}")`;
    }
    // adds consumption function for selected metrics if any
    if (query.mathOperationValue !== undefined && query.mathOperationValue.value !== '') {
      for (let i = 0; i < metricValueArr.length; i++) {
        if (metricValueArr[i] !== '') {
          newQuery +=
            sourceValueArr[i] === ''
              ? ' metrics  ' + `("${metricValueArr[i]}")` + `{${query.mathOperationValue.value}},`
              : ' metrics  ' +
              `("${metricValueArr[i]}"` +
              `,"${sourceValueArr[i]}")` +
              `{${query.mathOperationValue.value}},`;
        }
      }
      if (metricValueArr.length > 0 && metricValueArr.length - count !== 0) {
        newQuery = newQuery.slice(0, -1);
      }
    } else {
      for (let i = 0; i < metricValueArr.length; i++) {
        if (metricValueArr[i] !== '') {
          newQuery +=
            sourceValueArr[i] === ''
              ? ' metrics  ' + `("${metricValueArr[i]}"),`
              : ' metrics  ' + `("${metricValueArr[i]}"` + `,"${sourceValueArr[i]}"),`;
        }
      }
      if (metricValueArr.length > 0 && metricValueArr.length - count !== 0) {
        newQuery = newQuery.slice(0, -1);
      }
    }
    if (query.entityValue !== undefined) {
      newQuery += ' \nFROM ' + ' entities  ' + `(${query.entityValue.value})`;
    }

    if (arr.length !== 0) {
      if (attributeValueArr.length > 0) {
        let flag = 0;
        for (let i = 0; i < attributeValueArr.length; i++) {
          if (attributeFilterValueArr[i] !== '') {
            flag = 1;
            if (i === 0) {
              newQuery +=
                ' [ ' +
                'attributes ' +
                `('${attributeValueArr[i]}')` +
                `${attributeOperator[i]}` +
                `'${attributeFilterValueArr[i]}'`;
            } else {
              newQuery +=
                ' && ' +
                'attributes ' +
                `('${attributeValueArr[i]}')` +
                `${attributeOperator[i]}` +
                `'${attributeFilterValueArr[i]}'`;
            }
          }
        }
        if (flag === 1) {
          newQuery += ']';
        }
      }
      // adds tags value filter based on selected tag name 
      if (tagValueArr.length > 0) {
        let flag = 0;
        for (let i = 0; i < tagValueArr.length; i++) {
          if (tagFilterValueArr[i] !== '') {
            flag = 1;
            if (i === 0) {
              newQuery += ' [ ' + 'tags ' + `('${tagValueArr[i]}')` + `${tagOperator[i]}` + `'${tagFilterValueArr[i]}'`;
            } else {
              newQuery +=
                ' && ' + 'tags ' + `('${tagValueArr[i]}')` + `${tagOperator[i]}` + `'${tagFilterValueArr[i]}'`;
            }
          }
        }
        if (flag === 1) {
          newQuery += ']';
        }
      }
    }
    // checks for non-numeric granularity duration and uses appropriate UQL functions.
    if (query.granularity !== '' && query.granularity !== undefined) {
      if (/[a-zA-Z]/.test(query.granularity)) {
        newQuery += ' \nLIMITS ' + 'metrics.granularityDuration' + `(${query.granularity})`;
      } else {
        newQuery += ' \nLIMITS ' + 'metrics.granularityDataPoints' + `(${query.granularity}) `;
      }
    }

    newQuery += ' SINCE $__from UNTIL $__to ';

    this.setState({ finalQuery: newQuery });
    query.queryText = newQuery;
  };

  toggleSwitch = (event: any) => {
    const { onChange, query } = this.props;
    onChange({ ...query, viewQuery: event.target.checked });
  };

  /**
   * sets granularity for generating UQL query
   * @param event onChange event for granularity textbox on assited query workflow 
   */
  granularityHandler = (event: any) => {
    const { onChange, query } = this.props;
    onChange({ ...query, granularity: event.target.value });
    this.setState({}, () => {
      if (!this.state.isSourceError.includes(true)) {
        this.onFormQuery();
      }
    });
  };

  /**
   * Populate the metric component in metric array on '+' button click for assisted query workflow
   * @param event onClick event for '+' button on assited query workflow 
   */
  addMetricHandler(event: React.MouseEvent<HTMLElement>) {
    const { onChange, query } = this.props;
    let loadMetricArray = query.metricDivValue === undefined ? [] : query.metricDivValue;
    let loadSourceArray = query.sourceValueArray === undefined ? [] : query.sourceValueArray;
    let loadSourceOptions = query.sourceArrayOptions === undefined ? [] : query.sourceArrayOptions;

    onChange({
      ...query,
      metricDivValue: [...loadMetricArray, ''],
      sourceValueArray: [...loadSourceArray, ''],
      sourceArrayOptions: [...loadSourceOptions, ''],
    });
  }

  render() {
    const query = defaults(this.props.query, defaultQuery);
    const {
      queryText,
      id,
      entityValue,
      nameValue,
      queryTypeSelected,
      attributeValue,
      granularity,
      metricDivValue,
      sourceValueArray,
      mathOperationValue,
      sourceArrayOptions,
      attributeTagValue,
      attributeDivValue,
      operatorvalue,
      attributeTagFilterValue,
      viewQuery
    } = query;
    const options = [
      { label: 'Assisted Query Workflow', value: 'entities' },
      { label: 'Query Box', value: 'querybox' },
    ];
    let enableMetricDiv = metricDivValue === undefined ? [] : metricDivValue;
    let enableAttributeDiv = attributeDivValue === undefined ? [] : attributeDivValue;
    let displayViewQuery = viewQuery=== undefined ? true: viewQuery;
    let displayAssisted = (queryTypeSelected === 'entities' || queryTypeSelected === undefined)
    return (
      <>
        <div className="gf-form" style={{ marginBottom: 5.5, marginTop: 5 }}>
          <RadioButtonGroup
            options={options}
            value={queryTypeSelected === undefined ? 'entities' : queryTypeSelected}
            onChange={this.setQueryTypeEditor}
          />
        </div>
        {queryTypeSelected === 'querybox' && (
          <>
            <TextArea
              rows={6}
              name="Query Text"
              value={queryText || ''}
              placeholder="Input your UQL query here"
              onChange={this.onQueryTextChange}
            />
            <Button
              style={{ marginTop: 5, padding: 5 }}
              variant={(this.state.finalQuery === '') && (queryText === '' || queryText === undefined) ? 'secondary' : 'primary'}
              type="button"
              onClick={this.onRunQuery}
            >
              Run Query
            </Button>
          </>
        )}
        {displayAssisted && (
          <>
            <div className="gf-form">
              <div className="appd-inline">
                <Card className="appd-card">
                  <InlineLabel className="query-keyword width-5 label-center">FROM</InlineLabel>
                </Card>
              </div>
              <Card className="appd-card t.colors.background.primary">
                <div className="gf-form content-box">
                  <div className="appd-rmargin appd-flitermargin">Entities</div>
                  <div className="form_select">
                    <SelectComp
                      placeholder="Choose"
                      options={
                        this.state.nameArrayOptions.length > 0
                          ? this.state.nameArrayOptions
                          : [{ value: '', label: 'Choose Namespace...' }]
                      }
                      value={nameValue}
                      onChange={this.setNamespaceValue}
                    />
                  </div>
                  <div className="form_select">
                    <SelectComp
                      placeholder="Choose Entity Type"
                      options={
                        this.state.entityArrayOptions.length > 0
                          ? this.state.entityArrayOptions
                          : [{ value: '', label: 'Choose Entity Type...' }]
                      }
                      value={entityValue}
                      onChange={this.setEntityValue}
                    />
                  </div>

                  <div className="appd-rmargin">Granularity</div>
                  <div className="form_select">
                    <Input
                      name="Granularity"
                      value={granularity}
                      onChange={this.granularityHandler}
                      placeholder="Type Granularity..."
                    />
                  </div>
                </div>
              </Card>
            </div>
            <div className="gf-form">
              <div className="appd-inline">
                <Card className="appd-card">
                  <InlineLabel className="query-keyword width-5 label-center">FETCH</InlineLabel>
                </Card>
              </div>
              <Card className="appd-card">
                <div className="appd-idcontainer">
                  <div className="gf-form content-box">
                    <div className="appd-rmargin">ID</div>
                    <Switch value={id} onChange={this.onToggleId} transparent={true} />
                  </div>
                </div>
                <div style={{ width: '100%' }}>
                  {this.state.metricArrayOptions && (
                    <div className="ap-container">
                      {enableMetricDiv.map((data: any, _i: any) => {
                        return (
                          <div key={`attribute-${_i}`} className="ap-mdisplay">
                            <div key={_i} className="filter-box">
                              <div className="appd-rmargin">Metrics/Source</div>
                              <Tooltip content="Source represents Data source for metric. Source Selection will be disabled when sources are not available.However, Source selection is mandatory when any sources are available.">
                                <Icon className="info-icon" name="info-circle" size="sm" />
                              </Tooltip>

                              <div className="metric-select">
                                <SelectComp
                                  placeholder="Choose Metrics"
                                  options={
                                    this.state.metricArrayOptions.length > 0
                                      ? this.state.metricArrayOptions
                                      : [{ value: '', label: 'Choose Metrics...' }]
                                  }
                                  value={metricDivValue[_i]}
                                  onChange={(e) => {
                                    this.setMetricValue(e, _i);
                                  }}
                                />
                              </div>
                              <div className="metric-select">
                                <Select
                                  placeholder="Choose Source"
                                  disabled={sourceArrayOptions[_i].length === 0}
                                  value={sourceValueArray[_i]}
                                  options={
                                    sourceArrayOptions[_i].length > 0
                                      ? sourceArrayOptions[_i]
                                      : [{ value: '', label: 'Choose Source...' }]
                                  }
                                  onChange={(e) => this.setSourceValue(e, _i)}
                                />
                              </div>
                            </div>
                            <Button
                              className="ap-closebtn"
                              variant="secondary"
                              fill="solid"
                              size="md"
                              onClick={() => this.deleteMetrics(_i)}
                            >
                              <Icon name="times" size="lg" />
                            </Button>
                          </div>
                        );
                      })}
                      <Button
                        icon="plus"
                        variant="secondary"
                        type="button"
                        onClick={(e) => {
                          this.setState({
                            isSourceError: [...this.state.isSourceError, false],
                          });
                          this.addMetricHandler(e);
                        }}
                      >
                        Metrics
                      </Button>
                      {enableMetricDiv.length > 0 && (
                        <div className="filter-box">
                          <div className="appd-rmargin">Operations</div>
                          <SelectComp
                            placeholder="Choose Operations"
                            value={mathOperationValue}
                            options={
                              this.state.mathOperations.length > 0
                                ? this.state.mathOperations
                                : [{ value: '', label: 'Choose' }]
                            }
                            onChange={this.setConsumptionValues}
                          />
                        </div>
                      )}
                      <div>
                        <div className="filter-box" style={{ paddingRight: 10 }}>
                          <div className="appd-rmargin">Attributes</div>
                          <div className="attri_select">
                            <SelectComp
                              placeholder="Choose Attribute"
                              options={
                                this.state.attributeArrayOptions.length > 0
                                  ? this.state.attributeArrayOptions
                                  : [{ value: '', label: 'Choose Attribute...' }]
                              }
                              value={attributeValue}
                              onChange={this.setWithoutFilterAttributeValue}
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                  )}
                </div>
              </Card>
            </div>
            <div className="gf-form">
              <div className="appd-inline">
                <Card className="appd-card">
                  <InlineLabel className="query-keyword width-5 label-center">FILTER</InlineLabel>
                </Card>
              </div>
              <Card className="appd-card">
                <div className="ap-container">
                  {enableAttributeDiv.map((data: any, i: any) => {
                    if (data?.includes('attribute')) {
                      return (
                        <div key={`attribute-${i}`} className="ap-display">
                          <div className="filter-box">
                            <div className="appd-flitermargin">Attribute</div>
                            <div className="">
                              <SelectComp
                                placeholder="Choose"
                                options={this.state.attributeFilterArrayOptions}
                                value={attributeTagValue[i]}
                                onChange={(e) => this.filterAttributeHandler(e, i)}
                              />
                            </div>
                            <div className="">
                              <Select
                                options={[
                                  { value: '=', label: '=' },
                                  { value: '!=', label: '!=' },
                                ]}
                                onChange={(e) => this.setOperatorSelected(e, i)}
                                value={operatorvalue[i]}
                              />
                            </div>
                            <div className="">
                              <SelectComp
                                placeholder="Choose"
                                options={this.state.attributeFilterList[i]}
                                value={attributeTagFilterValue[i]}
                                onChange={(e) => this.setAttributeFilterListValue(e, i)}
                              />
                            </div>
                          </div>
                          <Button
                            className="ap-closebtn"
                            variant="secondary"
                            fill="solid"
                            size="md"
                            onClick={() => this.deleteAttributeTag(i)}
                          >
                            <Icon name="times" size="lg" />
                          </Button>
                        </div>
                      );
                    } else {
                      return (
                        <div key={`tag-${i}`} className="ap-display">
                          <div className="filter-box">
                            <div className="appd-flitermargin">Tag</div>
                            <div className="">
                              <SelectComp
                                placeholder="Choose"
                                options={this.state.tagsArrayOptions}
                                value={attributeTagValue[i]}
                                onChange={(e) => this.filterTagHandler(e, i)}
                              />
                            </div>
                            <div className="">
                              <Select
                                options={[
                                  { value: '=', label: '=' },
                                  { value: '!=', label: '!=' },
                                ]}
                                onChange={(e) => this.setOperatorSelected(e, i)}
                                value={operatorvalue[i]}
                              />
                            </div>
                            <div className="">
                              <SelectComp
                                placeholder="Choose"
                                options={this.state.tagFilterList[i]}
                                value={attributeTagFilterValue[i]}
                                onChange={(e) => this.setTagFilterListValue(e, i)}
                              />
                            </div>
                          </div>
                          <Button
                            className="ap-closebtn"
                            variant="secondary"
                            fill="solid"
                            size="md"
                            onClick={() => this.deleteAttributeTag(i)}
                          >
                            <Icon name="times" size="lg" />
                          </Button>
                        </div>
                      );
                    }
                  })}
                  <div className="addfilter-btn">
                    <ButtonCascader
                      hideDownIcon={true}
                      icon="plus"
                      variant="secondary"
                      onChange={this.handleCascaderChange}
                      options={[
                        { label: 'Attribute', value: 'Attribute' },
                        { label: 'Tags', value: 'Tags' },
                      ]}
                    />
                  </div>
                </div>
              </Card>
            </div>
            {this.state.isSourceError.includes(true) && <Alert title={'Select a source'} severity={'error'} />}
            <div className="gf-form bottom-wrapper">
              <div className="gf-form-inline">
                <Button
                  style={{ padding: 5, marginRight: 5 }}
                  variant={(this.state.finalQuery === '') && (queryText === '' || queryText === undefined) ? 'secondary' : 'primary'}
                  type="button"
                  onClick={this.onRunQuery}
                >
                  Run Query
                </Button>
                <Button className="appd-veiw" variant="secondary" type="button">
                  <span style={{ marginRight: '5px' }}>View Query</span>
                  <Switch value={displayViewQuery} onChange={this.toggleSwitch} />
                </Button>
              </div>
              {displayAssisted && displayViewQuery && (
                <div style={{ marginLeft: 5, width: '71%' }}>
                  <Input
                    name="important Textarea"
                    required
                    value={queryText}
                    onChange={this.onQueryTextChange}
                    label="Query Text"
                  />
                </div>
              )}
            </div>
          </>
        )}
      </>
    );
  }
}
