import { getBackendSrv, FetchError, getTemplateSrv } from '@grafana/runtime'; 
import _ from 'lodash';
import {
  DataSourceInstanceSettings,
  DataQueryRequest,
  DataQueryResponse,
  DataSourceApi,
  toDataFrame,
} from '@grafana/data';
import { MyQuery, AppdOptions, defaultQuery, AppDVariableQuery } from './types'; 
import defaults from 'lodash/defaults';
import { lastValueFrom } from 'rxjs';
import { Buffer } from 'buffer';
import { dataTranformationHandler } from './controllers/dataTransformation';

type ApiResponse = {
  tenantId: string;
  token_type: string;
  access_token: string;
  expires_in: number;
  1: any;
  0: any;
}

export class DataSource extends DataSourceApi<MyQuery, AppdOptions> {
  baseUrl: string;
  id: any;
  name: any;
  jsonData: any;
  secureJsonData: any;
  options: any;
  url?: string;
  token: string;
  uid: string;
  expiryTime: number;

  constructor(instanceSettings: DataSourceInstanceSettings<AppdOptions>) {
    super(instanceSettings);
    this.baseUrl = instanceSettings.url!;
    this.jsonData = instanceSettings.jsonData!;
    this.uid = instanceSettings.uid;
    this.token = '';
    this.expiryTime = 0;
  }

  async query(options: DataQueryRequest<MyQuery>): Promise<DataQueryResponse> {
    const promises = options.targets.map(async (target) => {
      const query = defaults(target, defaultQuery);
      query.queryText = getTemplateSrv().replace(query.queryText, options.scopedVars);
      if (query.queryText === undefined || query.queryText === '') {
        return [];
      }
  
      return this._metaRequest(`${query.queryText}`, false)
        .then((response: any) => {
          if (response.status !== 200) {
            throw response;
          }
          const queryText = `${query.queryText}`;
          const resp = response.data;

          const transformdArray = dataTranformationHandler(resp, queryText);

          const frame = toDataFrame({
            name: 'appDD',
            fields: transformdArray,
          });

          return frame;
        })
        .catch((err: FetchError) => {
          throw new Error(
            `${err.data.errorDetails[0].message}: ${err.data.errorDetails[0].fixPossibilities} : ${err.data.errorDetails[0].errorType}`
          );
        });
    });

    return Promise.all(promises).then((data) => ({ data }));
  }

  async metricFindQuery(query: AppDVariableQuery, options?: any) {
    // Retrieve DataQueryResponse based on query.
    if(query.rawQuery !== 'configURL'){
      const response = await this.fetchFilterValues(query.rawQuery, options);
      const values = response.length > 0 ? response[0].values.map((frame: any) => ({ text: frame })) : [{ text: '' }];    
      return values;
    }
    if(query.rawQuery === 'configURL'){
      const hostURLs = [{
        name:'url',
        type:'string',
        values:[this.jsonData.url]
      }];
      const sourceURLValues = hostURLs.length > 0 ? hostURLs[0].values.map((frame: any) => ({ text: frame })) : [{ text: '' }];
      return sourceURLValues;
    }
  }

/**
 * Execute 'query type' variable UQL query 
 * @param rawQuery Variable UQL query to be executed.
 * @param options 
 * @returns Dataframe array
 */
  async fetchFilterValues(rawQuery: string, options: any) {
    rawQuery = getTemplateSrv().replace(rawQuery, options.scopedVars);
    const resp = await this._metaRequest(rawQuery, false);
    const transformdArray: any = dataTranformationHandler(resp.data, rawQuery);
    return transformdArray;
  }

  /**
   * Makes a POST request for executing provided UQL.
   * @param enterdQuery UQL query to be executed.
   * @param generateToken A boolean flag. True value generates new token. False value re-uses token if it is not expired
   * @returns API response object with UQL output.
   */
  async _metaRequest(enterdQuery: string, generateToken: boolean) {
    await this.generateToken(generateToken);

    const headers = {
      authorization: this.jsonData.authorization,
      'appd-tid': this.jsonData.appdtid,
      'Content-Type': 'text/plain',
      Accept: 'application/json',
    };

    const observableResponse = getBackendSrv().fetch({
      url: this.jsonData.url + '/monitoring/v1/query/execute',
      method: 'POST',
      data: enterdQuery,
      headers: headers,
    });

    const response = await lastValueFrom(observableResponse);

    return response;
  }

  /**
   * Generates token, based on authentication type.
   * @param reGenrateToken boolean flag. True value re-generates new token. False value re-uses token if it is not expired
   */
  async generateToken(reGenrateToken: boolean) {
    if (!reGenrateToken) {
      if (this.expiryTime === 0 || new Date().getTime() >= this.expiryTime) {
        this.jsonData.authtype !== 'Basic' ? await this.tokenPostRequest() : await this.tokenBasicRequest();
      }
    } else {
      this.jsonData.authtype !== 'Basic' ? await this.tokenPostRequest() : await this.tokenBasicRequest();
    }
    this.jsonData.authorization = `bearer ${this.token}`;
  }

  /**
   * Generates token with POST authentication type.
   * @returns responseResult object for token API call
   */
  async tokenPostRequest() {
    let responseResult;
    const postHeader = {
      'Content-Type': 'application/x-www-form-urlencoded; charset=utf-8',
    };
    const postBody = `grant_type=client_credentials&client_id=${this.jsonData.clientId}&client_secret=${this.jsonData.secretKey}`;

    const result = getBackendSrv().fetch<ApiResponse>({
      method: 'POST',
      url: `${this.jsonData.url}/auth/${this.jsonData.appdtid}/default/oauth2/token`,
      headers: postHeader,
      data: postBody,
    });

    responseResult = await lastValueFrom(result);
    this.token = responseResult.data.access_token;
    this.expiryTime = new Date().getTime() + responseResult.data.expires_in * 1000;

    return responseResult;
  }

  /**
   * Generates token with BASIC authentication type.
   * @returns responseResult object for token API call
   */
  async tokenBasicRequest() {
    let responseResult;
    const postHeader = {
      'Content-Type': 'application/x-www-form-urlencoded; charset=utf-8',
      Authorization: 'Basic ' + Buffer.from(`${this.jsonData.clientId}:${this.jsonData.secretKey}`).toString('base64'),
    };
    const postBody = `grant_type=client_credentials`;

    const result = getBackendSrv().fetch<ApiResponse>({
      method: 'POST',
      url: `${this.jsonData.url}/auth/${this.jsonData.appdtid}/default/oauth2/token`,
      headers: postHeader,
      data: postBody,
    });
    
    responseResult = await lastValueFrom(result);
    this.token = responseResult.data.access_token;
    this.expiryTime = new Date().getTime() + responseResult.data.expires_in * 1000;

    return responseResult;
  }

  async testDatasource(): Promise<any> {
    const defaultErrorMessage = 'Could not establish connection, please try again';
    return await this._metaRequest('fetch attributes(k8s.cluster.name) from entities(k8s:cluster) since -1h', true)
      .then(() => {
        return { status: 'success', message: 'Connection successful' };
      })
      .catch((err: any) => {
        return {
          status: 'error',
          message: err.statusText ? `${err.data.message} ${err.data.error_description}` : defaultErrorMessage,
        };
      });
  }
}
