import { DataQuery, DataSourceJsonData } from '@grafana/data';

export interface MyQuery extends DataQuery {
  queryText?: string;
  constant: number;
  viewQuery: boolean,
  nameValue: any,
  entityValue: any,
  attributeValue: any
  tagFilterList: any,
  attributeFilterList: any,
  attributeTagValue: any,
  attributeTagFilterValue: any,
  finalQuery: string,
  nameArrayOptions: any[],
  entityArrayOptions: any[],
  attributeArrayOptions: any[]
  attributeFilterArrayOptions: any[],
  tagsArrayOptions: any,
  metricArrayOptions: any[],
  nameEntitiesMap: Map<any, any>,
  entitiesMatricMap: Map<any, any>,
  entitiesAttributeMap: Map<any, any>
  activate: boolean,
  queryTypeSelected: string,
  operatorvalue: any,
  granularity: any,
  id: boolean,
  attributeDivValue: any,
  metricDivValue: any,
  filterCascadeOptions: any,
  mathOperationValue: any
  mathOperations: any,
  sourceValueMap: Map<any, any>,
  sourceList: any,
  sourceArrayOptions: any,
  sourceValue: any,
  sourceValueArray: any,
}

export const defaultQuery: Partial<MyQuery> = {
  constant: 6.5,
};

export interface AppdOptions extends DataSourceJsonData {
  url: string;
  timeInterval: string;
  authtype: AppdAuthType;
  timescaledb: boolean;
  clientId: string;
  username: string;
  password: string;
  secretKey: string;
  appdtid: string;
  authorization: string;
  tenantName: string;
}

export interface AppDSecureJsonData extends DataSourceJsonData {
  secretKey?: string;
  password?: string;
}

export interface SecureField extends DataSourceJsonData {
  id: string;
  name: string;
  value: string;
  configured: boolean;
}

export enum AppdAuthType {
  basic = 'Basic',
  post = 'Post',
}

export interface AppDVariableQuery {
  rawQuery: string;
}
