import {
  DataSourcePluginOptionsEditorProps,
  SelectableValue,
  updateDatasourcePluginJsonDataOption,
} from '@grafana/data';
import React, { ChangeEvent } from 'react';
import { AppdOptions, AppdAuthType } from './types';
import { FieldSet, InlineField, Input, Select, LegacyForms } from '@grafana/ui';
import { HelpText } from './components/HelpText';

const { SecretFormField } = LegacyForms;

export const ConfigEditor = (props: DataSourcePluginOptionsEditorProps<AppdOptions>) => {
  const { options, onOptionsChange } = props;
  const labelWidthConnection = 20;
  const inputBoxWidth = 60;
  const secureBoxWidth = 30;
  const labelWidthsShort = 10;
  const jsonData = options.jsonData;
  const secureJsonFields = options.secureJsonFields;

  const authTypes: Array<SelectableValue<AppdAuthType>> = [
    { value: AppdAuthType.basic, label: 'Basic' },
    { value: AppdAuthType.post, label: 'Post' },
  ];

  const onPropertyChange = (event: ChangeEvent<HTMLInputElement>) => {
    const name = event.target.name;
    const jsonData = {
      ...options.jsonData,
      [name]: event.target.value,
    };
    onOptionsChange({ ...options, jsonData });
  };

  const onJSONDataOptionSelected = (property: keyof AppdOptions) => {
    return (value: SelectableValue) => {
      updateDatasourcePluginJsonDataOption(props, property, value.value);
    };
  };

  const onPasswordChange = (event: ChangeEvent<HTMLInputElement>) => {
    const name = event.target.name;
    onOptionsChange({
      ...options,
      jsonData: {
        ...options.jsonData,
        [name]: event.target.value,
      },
    });
  };

  const onResetSecretKey = () => {
    onOptionsChange({
      ...options,
      secureJsonFields: {
        ...options.secureJsonFields,
        secretKey: false,
      },
      secureJsonData: {
        ...options.secureJsonData,
        secretKey: '',
      },
    });
  };

  return (
    <>
      <h2 style={{ fontWeight: 'bold' }}> Metrics </h2>
      <hr style={{ borderTop: '2px solid #bbb', marginBottom: 30 }} />
      <FieldSet width={400}>
        <h4> HTTP </h4>
        <InlineField
          labelWidth={labelWidthConnection}
          label="URL"
          tooltip="AppDynamics Cloud Tenant URL"
        >
          <Input
            width={inputBoxWidth}
            name="url"
            type="text"
            value={jsonData.url || ''}
            placeholder="https://CloudTenantURL.com"
            onChange={onPropertyChange}
          />
        </InlineField>
      </FieldSet>

      <FieldSet width={400}>
        <h4> Auth </h4>
        <div className="gf-form-inline">
          <div className="gf-form">
            <InlineField
              labelWidth={labelWidthConnection}
              label="Auth Type"
              htmlFor="tlsMode"
            >
              <Select
                width={20}
                options={authTypes}
                inputId="authtypes"
                value={jsonData.authtype}
                onChange={onJSONDataOptionSelected('authtype')}
              ></Select>
            </InlineField>
          </div>
          <HelpText />
        </div>
      </FieldSet>

      <FieldSet style={{ marginBottom: 30 }} width={400}>
        <h4> Service Principal Details </h4>
        <InlineField labelWidth={labelWidthConnection} label="Tenant Id">
          <Input
            width={inputBoxWidth}
            name="appdtid"
            value={jsonData.appdtid || ''}
            placeholder="Please enter tenant id here"
            onChange={onPropertyChange}
          />
        </InlineField>
        <InlineField labelWidth={labelWidthConnection} label="Client Id">
          <Input
            width={inputBoxWidth}
            name="clientId"
            value={jsonData.clientId || ''}
            placeholder="Please enter client id here"
            onChange={onPropertyChange}
          />
        </InlineField>

        <SecretFormField
          name="secretKey"
          isConfigured={(secureJsonFields && secureJsonFields.secretKey) as boolean}
          label="Secret Key"
          labelWidth={labelWidthsShort}
          inputWidth={secureBoxWidth}
          onChange={onPasswordChange}
          onReset={onResetSecretKey}
          value={jsonData.secretKey || ''}
          placeholder="Please enter secret key here"
        />
      </FieldSet>
    </>
  );
};
